
:root{
  --bg:#0b1220;
  --panel:#0f172a;
  --panel-2:#111827;
  --muted:#94a3b8;
  --text:#e5e7eb;
  --primary:#22d3ee;
  --accent:#6366f1;
  --ok:#22c55e;
  --warn:#f59e0b;
  --danger:#ef4444;
  --chip:#1f2937;
  --border:#1f2937;
  --glass: rgba(255,255,255,0.06);
  --shadow: 0 10px 30px rgba(0,0,0,.35);
}

*{box-sizing:border-box}
html,body,#root{height:100%}
body{
  margin:0;
  font-family: ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Ubuntu, Cantarell, Noto Sans, Helvetica Neue, Arial, "Apple Color Emoji","Segoe UI Emoji";
  background: radial-gradient(1200px 600px at 80% -10%, #1e293b 0%, #0b1220 60%);
  color:var(--text);
}

.container{
  max-width:1100px;
  margin:32px auto;
  padding:0 16px;
}

.card{
  background: linear-gradient(180deg, rgba(255,255,255,.04), rgba(255,255,255,.02));
  border:1px solid var(--border);
  border-radius:14px;
  padding:18px;
  box-shadow: var(--shadow);
  backdrop-filter: blur(8px);
}

.header{
  display:flex; align-items:center; justify-content:space-between; gap:16px;
  margin-bottom:18px;
}
.brand{
  display:flex;align-items:center;gap:12px;
}
.brand .dot{
  width:14px;height:14px;border-radius:50%;
  background: conic-gradient(from 180deg, var(--primary), var(--accent), var(--primary));
  box-shadow:0 0 24px var(--primary);
}
.brand h1{margin:0;font-size:18px;letter-spacing:.4px;font-weight:600}
.badge{
  padding:6px 10px;border-radius:999px;background:var(--chip);color:var(--muted);font-size:12px;border:1px solid var(--border);
}

.grid{display:grid; gap:14px}
.grid.cols-2{grid-template-columns:1fr 1fr}
@media(max-width:860px){.grid.cols-2{grid-template-columns:1fr}}

.section-title{margin:8px 0 6px;font-size:13px;color:var(--muted);text-transform:uppercase;letter-spacing:.12em}

.row{display:flex;gap:10px;flex-wrap:wrap}
.input, select, textarea{
  width:100%; background:rgba(255,255,255,.04); color:var(--text);
  border:1px solid var(--border); border-radius:10px; padding:10px 12px; outline:none;
}
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button{ -webkit-appearance: none; margin:0;}
input[type="number"]{ -moz-appearance: textfield; }
textarea{min-height:90px; resize:vertical}
.help{font-size:12px;color:var(--muted);margin-top:6px}
.label{font-size:13px;color:var(--muted);margin-bottom:6px}
.field{display:flex;flex-direction:column;gap:6px;flex:1}

.switch{display:flex;gap:8px;align-items:center}
.switch input{appearance:none;width:36px;height:20px;border-radius:999px;position:relative;background:#1f2937;outline:none;border:1px solid var(--border);cursor:pointer}
.switch input:checked{background:linear-gradient(90deg,var(--accent),var(--primary))}
.switch input:before{content:"";position:absolute;top:2px;left:2px;width:14px;height:14px;border-radius:50%;background:#fff;transition:.2s}
.switch input:checked:before{left:20px}

.btn{
  padding:10px 14px;border-radius:10px;border:1px solid var(--border);background:#141a2b;color:var(--text);cursor:pointer
}
.btn.primary{background:linear-gradient(90deg,var(--accent),var(--primary));border:none;color:#04131b;font-weight:700}
.btn.ghost{background:transparent;border:1px dashed var(--border);color:var(--muted)}
.btn.ok{background:linear-gradient(90deg,#16a34a,#22c55e);border:none;color:#02140b;font-weight:700}
.btn.warn{background:linear-gradient(90deg,#ea580c,#f59e0b);border:none}
.btn.danger{background:linear-gradient(90deg,#b91c1c,#ef4444);border:none}

.kbd{font-family:ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;font-size:11px;background:#0b1220;border:1px solid var(--border);padding:3px 6px;border-radius:6px;color:var(--muted)}

.table{width:100%;border-collapse:separate;border-spacing:0 8px}
.table thead th{font-size:12px;color:var(--muted);text-align:left;font-weight:500}
.table tbody tr{background:rgba(255,255,255,.04);border:1px solid var(--border)}
.table tbody td{padding:10px 12px;border-top:1px solid var(--border);border-bottom:1px solid var(--border)}
.table tbody tr td:first-child{border-left:1px solid var(--border);border-top-left-radius:8px;border-bottom-left-radius:8px}
.table tbody tr td:last-child{border-right:1px solid var(--border);border-top-right-radius:8px;border-bottom-right-radius:8px}

.chips{display:flex;gap:6px;flex-wrap:wrap}
.chip{padding:6px 10px;font-size:12px;background:var(--chip);border:1px solid var(--border);border-radius:999px;color:var(--muted)}

footer{margin:26px 0 10px;color:var(--muted);font-size:12px;text-align:center}
